import Vue from 'vue'
import Router from 'vue-router'
import Index from '../components/Index'
import Home from '../components/Home'
import Renwu from '../components/Renwu'
import Wuqi from '../components/Wuqi'
import Tianfu from '../components/Tianfu'

Vue.use(Router)

export default new Router({
  mode:'history',
  base:'/vue',
  routes: [
    {
      path: '/',
      name: 'index',
      component:Index,
      redirect:'/index'
    },
    {
      path: '/index',
      name: 'index',
      component:Index,
      children:[
        {
          path: '/home',
          name: 'Home',
          component:Home
        },
        {
          path: '/wuqi',
          name: 'Wuqi',
          component:Wuqi
        },
        {
          path: '/renwu',
          name: 'Renwu',
          component:Renwu
        },
        {
          path: '/tianfu',
          name: 'Tianfu',
          component:Tianfu
        }
      ]
    }
  ]
})



