<template>
    <el-main>
      <!--  普攻等级 row  -->
      <el-row :gutter="20" class="el-main-el-row">
        <el-col :xs="24" :sm="12">
          <h2>普攻等级 A</h2>
        </el-col>
        <el-col :xs="24" :sm="12">
          <div class="block">
            <el-slider
              v-model="a_fanwei"
              range
              show-stops
              :min="1"
              :max="10"
              :marks="marks_a">
            </el-slider>
          </div>
        </el-col>
      </el-row>
      <!--  元素战技等级 row  -->
      <el-row :gutter="20" class="el-main-el-row">
        <el-col :xs="24" :sm="12">
          <h2>技能战技 E</h2>
        </el-col>
        <el-col :xs="24" :sm="12">
          <div class="block">
            <el-slider
              v-model="e_fanwei"
              range
              show-stops
              :min="1"
              :max="10"
              :marks="marks_eq">
            </el-slider>
          </div>
        </el-col>
      </el-row>
      <!--  元素爆发等级 row  -->
      <el-row :gutter="20" class="el-main-el-row">
        <el-col :xs="24" :sm="12">
          <h2>技能爆发 Q</h2>
        </el-col>
        <el-col :xs="24" :sm="12">
          <div class="block">
            <el-slider
              v-model="q_fanwei"
              range
              show-stops
              :min="1"
              :max="10"
              :marks="marks_eq">
            </el-slider>
          </div>
        </el-col>
      </el-row>
      <!-- 确认按钮 -->
      <div class="btn_div">
        <el-button
          type="primary"
          icon="el-icon-check"
          @click="jisuantianfu"
          style="width: 50%; max-width: 250px;"
        >计算</el-button>
      </div>

      <el-row :gutter="20">
        <el-col :xs="24" :sm="8">
          <el-card class="box-card" shadow="hover">
            <div slot="header" class="clearfix">
              <h2>天赋书</h2>
            </div>
            <el-row>
              <el-col :xs="12" :sm="12">
                <img src="../assets/img/tianfu/90px-「抗争」的教导.png">
              </el-col>
              <el-col :xs="12" :sm="12" style="line-height: 97px">
                绿色天赋书： {{ipt_tianfu_1}}
              </el-col>
            </el-row>
            <el-row>
              <el-col :xs="12" :sm="12">
                <img src="../assets/img/tianfu/90px-「抗争」的指引.png">
              </el-col>
              <el-col :xs="12" :sm="12" style="line-height: 97px">
                蓝色天赋书： {{ipt_tianfu_2}}
              </el-col>
            </el-row>
            <el-row>
              <el-col :xs="12" :sm="12">
                <img src="../assets/img/tianfu/90px-「抗争」的哲学.png">
              </el-col>
              <el-col :xs="12" :sm="12" style="line-height: 97px">
                紫色天赋书： {{ipt_tianfu_3}}
              </el-col>
            </el-row>
          </el-card>
        </el-col>
        <el-col :xs="24" :sm="8">
          <el-card class="box-card" shadow="hover">
            <div slot="header" class="clearfix">
              <h2>怪物掉落物</h2>
            </div>
            <el-row>
              <el-col :xs="12" :sm="12">
                <img src="../assets/img/tianfu/90px-破损的面具.png">
              </el-col>
              <el-col :xs="12" :sm="12" style="line-height: 97px">
                灰色掉落物： {{ipt_guaiwu_1}}
              </el-col>
            </el-row>
            <el-row>
              <el-col :xs="12" :sm="12">
                <img src="../assets/img/tianfu/90px-污秽的面具.png">
              </el-col>
              <el-col :xs="12" :sm="12" style="line-height: 97px">
                绿色掉落物： {{ipt_guaiwu_2}}
              </el-col>
            </el-row>
            <el-row>
              <el-col :xs="12" :sm="12">
                <img src="../assets/img/tianfu/90px-不祥的面具.png">
              </el-col>
              <el-col :xs="12" :sm="12" style="line-height: 97px">
                蓝色掉落物： {{ipt_guaiwu_3}}
              </el-col>
            </el-row>
          </el-card>
        </el-col>
        <el-col :xs="24" :sm="8">
          <el-card class="box-card" shadow="hover">
            <div slot="header" class="clearfix">
              <h2>其它材料</h2>
            </div>
            <el-row>
              <el-col :xs="12" :sm="12">
                <img src="../assets/img/tianfu/90px-东风之翎.png">
              </el-col>
              <el-col :xs="12" :sm="12" style="line-height: 97px">
                周本材料： {{ipt_qita_1}}
              </el-col>
            </el-row>
            <el-row>
              <el-col :xs="12" :sm="12">
                <img src="../assets/img/tianfu/90px-智识之冕.png">
              </el-col>
              <el-col :xs="12" :sm="12" style="line-height: 97px">
                智识之冕： {{ipt_qita_2}}
              </el-col>
            </el-row>
            <el-row>
              <el-col :xs="12" :sm="12">
                <img src="../assets/img/tianfu/90px-摩拉.png">
              </el-col>
              <el-col :xs="12" :sm="12" style="padding-top: 15px">
                摩拉数量:&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<br>
                &nbsp;<br>
                {{ipt_qita_3}}
              </el-col>
            </el-row>
          </el-card>
        </el-col>
      </el-row>
    </el-main>
</template>

<script>
    export default {
        name: "Tianfu",
        data() {
          return {
            a_fanwei: [1, 1],
            e_fanwei: [1, 1],
            q_fanwei: [1, 1],
            marks_a: {
              1 : {
                style: {
                  color: '#1989FA'
                },
                label: this.$createElement('strong', '1')
              },
              6 : '试试滑动两端滑块',
              10 :{
                style: {
                  color: '#1989FA'
                },
                label: this.$createElement('strong', '10')
              }
            },
            marks_eq: {
              1 : {
                style: {
                  color: '#1989FA'
                },
                label: this.$createElement('strong', '1')
              },
              10 :{
                style: {
                  color: '#1989FA'
                },
                label: this.$createElement('strong', '10')
              }
            },
            ipt_tianfu_1:"-",
            ipt_tianfu_2:"-",
            ipt_tianfu_3:"-",
            ipt_guaiwu_1:"-",
            ipt_guaiwu_2:"-",
            ipt_guaiwu_3:"-",
            ipt_qita_1:"-",
            ipt_qita_2:"-",
            ipt_qita_3:"-",
            allneed_info_arr:[0, 0, 0, 0, 0, 0, 0, 0, 0]
          }
        },
        methods: {
          jisuantianfu(){
            this.allneed_info_arr = [0, 0, 0, 0, 0, 0, 0, 0, 0]
            var a_start = Number(this.a_fanwei[0]);
            var a_end = Number(this.a_fanwei[1]);

            var e_start = Number(this.e_fanwei[0]);
            var e_end = Number(this.e_fanwei[1]);

            var q_start = Number(this.q_fanwei[0]);
            var q_end = Number(this.q_fanwei[1]);

            if (a_start == 1 && a_end == 1 && e_start ==1
            && e_end ==1 && q_start == 1 && q_end == 1){
              this.ipt_tianfu_1 = "-";
              this.ipt_tianfu_2 = "-";
              this.ipt_tianfu_3 = "-";
              this.ipt_guaiwu_1 = "-";
              this.ipt_guaiwu_2 = "-";
              this.ipt_guaiwu_3 = "-";
              this.ipt_qita_1 = "-";
              this.ipt_qita_2 = "-";
              this.ipt_qita_3 = "-";
              this.$message({
                message:"您还未选择升级的天赋哦! 请试着拖动进度条两端的滑块",
                type: 'warning'
              })
            }
            else {
              // 消耗材料统计
              while (a_start < a_end){
                a_start += 1;
                this.allneed_info_arr = this.arradd(this.allneed_info_arr, this.dj_info(a_start));
              }

              while (e_start < e_end){
                e_start += 1;
                this.allneed_info_arr = this.arradd(this.allneed_info_arr, this.dj_info(e_start));
              }

              while (q_start < q_end){
                q_start += 1;
                this.allneed_info_arr = this.arradd(this.allneed_info_arr, this.dj_info(q_start));
              }

              // 填入输入框
              this.ipt_tianfu_1 = this.allneed_info_arr[0];
              this.ipt_tianfu_2 = this.allneed_info_arr[1];
              this.ipt_tianfu_3 = this.allneed_info_arr[2];
              this.ipt_guaiwu_1 = this.allneed_info_arr[3];
              this.ipt_guaiwu_2 = this.allneed_info_arr[4];
              this.ipt_guaiwu_3 = this.allneed_info_arr[5];
              this.ipt_qita_1 = this.allneed_info_arr[6];
              this.ipt_qita_2 = this.allneed_info_arr[7];
              this.ipt_qita_3 = this.allneed_info_arr[8];
            }
          },
          // 每个等级各材料
          dj_info(n){
            if(n == 2){
              return [3, 0, 0, 6, 0, 0, 0, 0, 12500];
            }
            else if(n == 3){
              return [0, 2, 0, 0, 3, 0, 0, 0, 17500];
            }
            else if(n == 4){
              return [0, 4, 0, 0, 4, 0, 0, 0, 25000];
            }
            else if(n == 5){
              return [0, 6, 0, 0, 6, 0, 0, 0, 30000];
            }
            else if(n == 6){
              return [0, 9, 0, 0, 9, 0, 0, 0, 37500];
            }
            else if(n == 7){
              return [0, 0, 4, 0, 0, 4, 1, 0, 120000];
            }
            else if(n == 8){
              return [0, 0, 6, 0, 0, 6, 1, 0, 260000];
            }
            else if(n == 9){
              return [0, 0, 12, 0, 0, 9, 2, 0, 450000];
            }
            else if(n == 10){
              return [0, 0, 16, 0, 0, 12, 2, 1, 700000];
            }
            else{
              return [0, 0, 0, 0, 0, 0, 0, 0, 0];
            }
          },
          arradd(a, b) {
            var need_info_arr = new Array();
            for (var i = 0; i < 9; i++){
              need_info_arr[i] = a[i] + b[i];
            }
            return need_info_arr;
          }
        }
    }
</script>

<style scoped>
  .el-main-el-row{
    margin: 20px 0;
    border-radius: 20px;
    box-shadow: 0 2px 12px 0 rgba(64, 158, 255, 0.1);
  }
  .el-main-el-row .el-col{
    margin: 1rem 0;
  }
  h2{
    margin: 0;
    font-size: 20pt;
    color: #212529;
  }
  .block{
    width: 80%;
    margin-left: 10%;
  }
  .btn_div{
    margin:40px 0;
  }
</style>



