<template>
  <el-container>
    <el-header>
      <el-menu
        :default-active="defaultActive"
        router
        class="el-index-nav"
        mode="horizontal"
        active-text-color="#409EFF"
        @select="handleSelect">
        <el-menu-item index="/home" path="">主页</el-menu-item>
        <el-menu-item index="/renwu">人物</el-menu-item>
        <el-menu-item index="/wuqi">武器</el-menu-item>
        <el-menu-item index="/tianfu">天赋</el-menu-item>
      </el-menu>
    </el-header>

    <router-view/>

    <el-footer>
      <p>
        互联网ICP备案号：
        <a href="http://beian.miit.gov.cn/">
          鄂ICP备2022000362号-1
        </a>
      </p>
      <p>
        <img src="../assets/img/bg/备案图标.png" width="15px" height="15px" style="vertical-align: middle;">
        <a href="http://www.beian.gov.cn/portal/registerSystemInfo?recordcode=42098402000180">
          鄂公网安备 42098402000180号
        </a>
      </p>
      <p>
        &copy; 2021 Templeton Personal studio
      </p>
    </el-footer>
  </el-container>
</template>

<script>
    export default {
      name: "index",
      methods: {
        handleSelect (key, keyPath) {
        }
      },
      computed: {
        defaultActive() {
          return '/' + this.$route.path.split('/').reverse()[0];
        }
      }
    }
</script>

<style scoped>
  .el-menu-item{
    font-size: 16pt;
  }
  .el-footer{
    margin-top: 50px;
  }
  .el-footer p{
    font-size: 10pt;
    color: #909399;
    padding: 2px 0;
    margin: 0;
  }
  .el-footer a{
    text-decoration: none;
    color: #909399;
  }
  .el-footer a:hover{
    color: #409EFF;
  }
</style>



